<script>
	import loading from '@/components/wyb-loading/wyb-loading.vue'
	export default {
		data() {
			return {
				inputValue: "",
				catTopFiveList: [{
					name: "",
					avatarUrl: null,
					id: ""
				}, {
					name: "",
					avatarUrl: null,
					id: ""
				}, {
					name: "",
					avatarUrl: null,
					id: ""
				}, {
					name: "",
					avatarUrl: null,
					id: ""
				}, {
					name: "",
					avatarUrl: null,
					id: ""
				}],
				lock: false
			}
		},
		components: {
			loading
		},
		onLoad() {
			this.getCatTopFive();
		},
		methods: {
			search() {
				if (this.lock) {
					return;
				}
				this.lock = true;
				uni.request({
					url: this.$serverUrl + '/kitty/selectKittyByName',
					method: 'POST',
					data: {
						name: this.inputValue
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						if (res.data.code == 200) {
							if (res.data.data == "kittyNotFound") {
								uni.showToast({
									icon: 'none',
									title: 'æ²¡æœ‰æ‰¾åˆ°å°çŒ«å’ªå“¦~',
									duration: 1000
								});
							} else {
								this.toCatDetail(res.data.data);
								this.inputValue = "";
							}

						}
						this.lock = false;
					},
					fail: res => {
						setTimeout(() => {
							this.lock = false;
						}, 4000);
					}
				});
			},
			toCatDetail(id) {
				if (id == "") {
					return;
				}
				uni.navigateTo({
					url: '/pages/catdetail/catdetail?id=' + id
				});
			},
			getCatTopFive() {
				this.$refs.loading.showLoading();
				uni.request({
					url: this.$serverUrl + '/kitty/selectKittyTopFive',
					method: 'POST',
					success: res => {
						if (res.data.code == 200) {
							this.catTopFiveList = res.data.data;
							this.$refs.loading.hideLoading();
						}
					},
					fail: res => {
						setTimeout(() => {
							this.$refs.loading.hideLoading();
						}, 4000);
					}
				});
			},
			toAllCats() {
				uni.navigateTo({
					url: '/pages/allcats/allcats'
				});
			}
		}
	}
</script>

